import { forwardRef, useImperativeHandle, useState } from 'react';
import { Button, Modal } from 'react-bootstrap';

import './Birthday.css';

const Birthday = forwardRef((props: any, ref) => {

  const [show, setShow] = useState(false);
  const [clickCount, setClickCount] = useState(0);

  const onClickPresent = () => {
    setClickCount(clickCount + 1);
    setShow(true);
    props.onClickCountChange(true);
  };

  useImperativeHandle(ref, () => ({
    getClickCount() { return clickCount; },
  }));

  const handleClose = () => setShow(false);

  return (
    <>
      <div className="birthday-container">
        <div className="presents">
          <div onClick={() => onClickPresent()} className="present pink">
            <div className="lid"></div>
            <div className="box"></div>
            <div className="bow"></div>
            <div className="ribbon"></div>
          </div>
          <div onClick={() => onClickPresent()} className="present yellow">
            <div className="lid"></div>
            <div className="box"></div>
            <div className="bow"></div>
            <div className="ribbon"></div>
          </div>
          <div onClick={() => onClickPresent()} className="present purple">
            <div className="lid"></div>
            <div className="box"></div>
            <div className="bow"></div>
            <div className="ribbon"></div>
          </div>
        </div>
      </div>
      <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false}>
        <Modal.Header closeButton>
          <Modal.Title>Happy Birthday</Modal.Title>
        </Modal.Header>
        <Modal.Body>Woohoo, you're reading this text in a modal!</Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
});

export default Birthday;
