import { forwardRef, useImperativeHandle, useState } from 'react';

import './AudioButton.css';

interface AudioButtonProps {
  name: string;
  path: string;
  description: string;
  onClick: Function
}

export default forwardRef((props: AudioButtonProps, ref) => {
  const [progress, setProgress] = useState(0);
  const [playing, setPlaying] = useState(false);
  useImperativeHandle(ref, () => ({
    id() { return props.name; },
    getProgress() {
      return progress;
    },
    updateProgress(progress: number) {
      setProgress(progress);
    },
    updatePlaying(isPlaying: boolean) {
      setPlaying(isPlaying);
    }
  }));

  return (
    <button
      className={`mirea-btn ${playing && 'active'}`}
      onClick={() => props.onClick()}
      disabled={playing}
    >
      <div className="back"><div className="rotating">üå∏</div></div>
      <div className="front">{props.description}</div>
    </button>
  )
});
