import { FunctionComponent } from 'react';
import { Nav, Navbar } from 'react-bootstrap';
import { useTranslation } from 'react-i18next'
import { NavDropdown } from 'react-bootstrap';

const Navbars: FunctionComponent<{ isAnniversary: boolean, isBirthday: boolean }> = ({isAnniversary, isBirthday}) => {
  const { t, i18n } = useTranslation();

  const changeLng = (code: string) => {
    i18n.changeLanguage(code);
  };

  return (
    <Navbar bg="dark" variant="dark" expand="lg" className={`${(isAnniversary && 'anniversary ') || ''}${(isBirthday && 'birthday') || ''}`}>
      <Navbar.Brand href={`/`} title={t('site.title')}>
        <img
          alt={t('site.title')}
          src="/android-chrome-192x192.png"
          width="32"
          height="32"
          className="d-inline-block align-top"
        />{' '}
        {t('site.title')}
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link href={`/about`}>{t('pages.about.text')}</Nav.Link>
        </Nav>
      </Navbar.Collapse>
      <Nav>
        <NavDropdown title={t('site.switch_language')} id="nav-dropdown">
          <NavDropdown.Item onClick={() => changeLng('en')}>en</NavDropdown.Item>
          <NavDropdown.Item onClick={() => changeLng('ja')}>ja</NavDropdown.Item>
        </NavDropdown>
      </Nav>
    </Navbar>
  )
};

export default Navbars;
