const About: FunctionComponent = () => {
  const { t, i18n } = useTranslation();
  const metaInfo = {
    title: t('pages.about.title'),
    description: t('pages.about.description'),
    seo: {
      image: `${process.env.REACT_APP_GITHUB_URL}/${t('pages.home.image')}`,
      url: `${process.env.REACT_APP_GITHUB_URL}/${i18n.language}/about`,
      path: '/about',
      siteUpdatedAt: new Date(),
    }
  };
  return (
    <Fragment>
      <MetaInfo {...metaInfo} />
      <AboutInfo />
      <Row>
        <Col>
          <Card className="mt-2 mb-2">
            <Card.Body>
              <Card.Title>Emoji</Card.Title>
              <Row>
                <Col xs lg="6">
                  <Table bordered hover responsive>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Code</th>
                      </tr>
                    </thead>
                    <tbody>
                      {emoji_list.map((emoji: Emoji) => (
                        <tr key={emoji.name}>
                          <td>
                            {emoji.image_urls.map((url: string) => (
                              url.indexOf('img/') >= 0 ? (
                                <img
                                  key={url}
                                  width={32}
                                  height={32}
                                  className="mr-3"
                                  src={url}
                                  alt={emoji.code.join('')}
                                />) : (<span key={url}>{url}</span>)
                            ))}
                          </td>
                          <td>
                            {emoji.code.map((code: string, index: number) => (
                              <Fragment key={index} >
                                <CopyToClipboard text={code}>
                                  <code role="button">{code}</code>
                                </CopyToClipboard>
                                {' '}
                                < CopyToClipboard text={code} >
                                  <FontAwesomeIcon title="Copy code" role="button" icon={['far', 'copy']} />
                                </CopyToClipboard>
                              </Fragment>
                            ))}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </Card.Body>
            {/* <Card.Img variant="bottom" src="/img/og-image.jpg" /> */}
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
}

export default About;
