import { FunctionComponent, Fragment, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Navbars from './Navbars';
import Footer from './Footer';
import { useLocation } from 'react-router-dom';
import { getLocaleFromPath, isAnniversary, isBirthday } from '../utils';
import { useTranslation } from 'react-i18next';

const Layout: FunctionComponent = ({ children }) => {
  const { i18n } = useTranslation();
  const location = useLocation();
  const _isAnniversary = isAnniversary();
  const _isBirthday = isBirthday();
  useEffect(() => {
    const locale = getLocaleFromPath(location.pathname);
    if (locale) {
      i18n.changeLanguage(locale);
    }
  }, [i18n, location.pathname]);
  return (
    <Fragment>
      <Navbars isAnniversary={_isAnniversary} isBirthday={_isBirthday} />
      <Container fluid className={`${(_isAnniversary && 'anniversary ') || ''}${(_isBirthday && 'birthday') || ''}`}>
        {children}
      </Container>
      <Footer  isAnniversary={_isAnniversary} isBirthday={_isBirthday} />
    </Fragment>
  );
};

export default Layout;
