import { FunctionComponent, Fragment, useState, createRef } from 'react';
import { useTranslation } from 'react-i18next'
import MetaInfo from '../../components/MetaInfo';
import Buttons from '../../components/Buttons';
import Clips from '../../components/Clips';
import Sings from '../../components/Sings';
import Birthday from '../../components/Birthday';

import { Col, Row } from 'react-bootstrap';
import { isAnniversary, isBirthday } from '../../utils';

import './Home.css';

const Home: FunctionComponent = () => {
  const { t, i18n } = useTranslation();
  const metaInfo = {
    title: t('pages.home.title'),
    description: t('pages.home.description'),
    seo: {
      image: `${process.env.REACT_APP_GITHUB_URL}/${t('pages.home.image')}`,
      url: `${process.env.REACT_APP_GITHUB_URL}/${i18n.language}`,
      path: '',
      siteUpdatedAt: new Date(),
    }
  };

  const _isAnniversary = isAnniversary();
  const _isBirthday = isBirthday();
  const birthdayRef = createRef();
  const [showOverlay, setShowOverlay] = useState(_isAnniversary || _isBirthday ? true : false);
  const handleHideOverlay = () => setShowOverlay(false);

  return (
    <Fragment>
      <div id="overlay" onClick={handleHideOverlay} style={{display: showOverlay ? 'block' : 'none'} as React.CSSProperties}></div>
      <MetaInfo {...metaInfo} />
      <Row>
        <Col>
          {_isAnniversary ? null : null}
          {_isBirthday ? <Birthday ref={birthdayRef} onClickCountChange={handleHideOverlay}/> : null}
        </Col>
      </Row>
      <Row>
        <Col>
          <Buttons />
        </Col>
      </Row>
      <Row>
        <Col>
          <Sings />
        </Col>
      </Row>
      <Row>
        <Col>
          <Clips />
        </Col>
      </Row>
    </Fragment>
  );
}

export default Home;
